/*Make the scrolling smooth*/
* {
  scroll-behavior: smooth;
}
body {
  padding: 0;
  margin: 0;
  width: 100%;
  background-image: linear-gradient(to right, #c7d8e7, rgb(248, 248, 255));
}

.App {
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  background-color: #53565a;
}

.App form {
  display: block;
  position: relative;
}

.App form:after {
  content: "";
  display: block;
  position: absolute;
  top: -5px;
  left: -5px;
  bottom: -5px;
  right: -5px;
  z-index: 1;
  background-image: linear-gradient(
    to bottom right,
    rgb(127, 255, 212),
    #c7d8e7
  );
}

.App form .form-inner,
.register {
  position: relative;
  display: block;
  background-color: #fff;
  padding: 30px;
  z-index: 2;
}

.App form .form-inner h2 {
  color: #888;
  font-size: 28px;
  font-weight: 500;
  margin-bottom: 30px;
}

.App form .form-inner .form-group {
  display: block;
  width: 300px;
  margin-bottom: 15px;
}

.form-inner .form-group label {
  display: block;
  color: #000;
  font-size: 14px;
  margin-bottom: 5px;
  transition: 0.4s;
}
.form-inner .form-group:focus-within label {
  color: #0d659d;
}

.App form .form-inner .form-group input,
.register {
  display: block;
  width: 100%;
  padding: 10px 0px;
  background-color: #f8f8f8;
  border-radius: 8px;
  transition: 0.4s;
}

.App form .form-inner .form-group input:focus,
.register:focus {
  box-shadow: 0px 0px 3px rgba(0, 0, 0, 0.2);
}

.App form .form-inner input[type="submit"],
.welcome button,
.register {
  display: inline-block;
  padding: 10px 15px;
  border-radius: 8px;
  background-image: linear-gradient(
    to right,
    #6bcae2 50%,
    #6bcae2 50%,
    #0d659d
  );
  background-size: 200%;
  background-position: 0%;
  transition: 0.4s;
  color: #fff;
  font-weight: 700;
  cursor: pointer;
}

.App form .form-inner input[type="submit"]:hover,
.welcome button:hover,
.register:hover {
  background-position: 100% 0%;
}

.error {
  text-align: center;
  color: red;
}

.welcome {
  width: 100%;
  max-width: 480px;
  background-color: #fff;
  padding: 15px 30px;
}

.welcome h2 {
  text-align: center;
  color: #888;
  font-size: 28px;
  font-weight: 500;
  margin-bottom: 30px;
}

.welcome h2 span {
