:root {
  --colscheme1: #050b1f;
  --colscheme2: #6bcae2;
  --colscheme3: #0d659d;
  --colscheme4: #c7d8e7;
  --colscheme5: #11f7ff;
}

.notes * {
  margin: 0;
  padding: 0;
}
.notes {
  width: 80%;
  margin: auto;
  margin-bottom: 100px;
}
.notes p {
  font-family: calibri;
  margin: 20px 0;
}

.notes pre {
  background: #050b1f;
  background: var(--colscheme1);
  color: #11f7ff;
  color: var(--colscheme5);
  padding: 30px 50px;
  border-radius: 10px;
  overflow-x: auto;
  box-shadow: 0 5px 5px #0d659d;
  box-shadow: 0 5px 5px var(--colscheme3);
}

.notes pre::-webkit-scrollbar {
  height: 20px;
  background: #050b1f;
  background: var(--colscheme1);
  width: 5px;
  border-radius: 20px;

  padding: 5px;
}
.notes pre::-webkit-scrollbar-thumb {
  background: #0d659d;
  background: var(--colscheme3);

  border-radius: 10px;
  box-shadow: inset 0 0 5px #c7d8e7;
  box-shadow: inset 0 0 5px var(--colscheme4);
  width: 3px;
  border: 6px solid black;
}
.notes h1 {
  margin: 40px auto 40px 0;
  font-family: Calibri;
  font-size: 50px;
}

#fontsizer {
  font-family: calibri;
  width: 100%;
  display: block;

  overflow: auto;
}
#fontsizer ul {
  list-style-type: none;
}
#fontsizer li {
  text-align: center;
  display: inline-block;
  float: left;
  background: #6bcae2;
  background: var(--colscheme2);
  margin: 0;
  width: 50px;
  height: 50px;
  line-height: 50px;
}
#fontsizer li:hover {
  background: #c7d8e7;
  background: var(--colscheme4);
  cursor: pointer;
}
#fontsizer li:nth-child(2) {
  font-size: 20px;
}

#fontsizer li:nth-child(3) {
  font-size: 30px;
}

.comment {
  color: #bdbdbd;
}

/* Import Google Font */

.ppr {
  font-family: "Baloo Bhaina 2", cursive;
  background-color: white;
}

.ppr pre {
  color: white;
  background-color: #050b1f;
}

#italize {
  font-style: italic;
  font-size: 15px;
}
.container-fluid {
  width: 60%;
  margin: 0 auto;
  margin-top: 100px;
}

#center-page {
  text-align: center;
  background-color: #0d659d;
}
.container-fluid h1 {
  color: #6bcae2;
  position: relative;
  width: 23rem;
}

.container-fluid h1 ::after {
  position: absolute;
  bottom: 0;
  right: 12px;
  width: 67px;
  height: 2px;
  background-color: #6bcae2;
}

.icon_accordion {
  width: 100%;
  padding: 0 5px;
  border: 2px solid #0d659d;
  cursor: pointer;
  border-radius: 50px;
  display: -webkit-flex;
  display: flex;
  margin: 10px 0;
  -webkit-align-items: center;
          align-items: center;
}

.icon_accordion .icon {
  margin: 0 10px 0 0;
  width: 30px;
  height: 30px;
  background: #0d659d;
  border-radius: 50%;
  float: left;
  transition: all 0.5s ease-in;
}

.icon_accordion h5 {
  font-size: 15px;
  margin: 0;
  padding: 3px 0 0 0;
  font-weight: normal;
  color: #1f5c9a;
  transition: all 0.5s ease-in;
}

.active .icon {
  background: #fff;
  z-index: 1;
}

.active {
  background-color: #0d659d;
  color: #fff;
}

.active h5 {
  color: #fff;
}

.panel {
  padding: 0 18px;
  border-left: 1px solid #0d659d;
  margin-left: 25px;
  font-size: 14px;
  text-align: justify;
  overflow: hidden;
  max-height: 0px;
  transition: all 0.5s ease-in;
}

